import { Component, OnInit, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from "@angular/core";

import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { AreaService } from "../shared/area/area.service";
import { LocationService } from "../shared/location/location.service";
import { Area } from "../shared/area/area";

@Component({
  selector: "app-area-of-operation",
  templateUrl: "./area-of-operation.component.html",
  styleUrls: ["./area-of-operation.component.scss"],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class AreaOfOperationComponent implements OnInit {
  @ViewChild("displayCard") displayCard;
  @ViewChild("deleteCard") deleteCard;
  @ViewChild("createAreaModal") createAreaModal;
  @ViewChild("aooForm") aooForm;
  @ViewChild("body") body;

  public areas: BehaviorSubject<Array<Area>>;
  public areaCount: number;
  public areaForCreate: Area;
  public areaForDelete: Area;
  public areaDetails: Area;

  public fetchDone: boolean;

  public countries: BehaviorSubject<Array<any>>;
  public states: BehaviorSubject<Array<any>>;
  public cities: BehaviorSubject<Array<any>>;
  public selectedCountry;
  public selectedState;
  public selectedCities;
  public searchQuery: string;
  public citiesFetched: boolean;

  public countryActive: boolean;
  public StateActive: boolean;
  public CitiesActive: boolean;

  constructor(
    public areaService: AreaService,
    private locationService: LocationService,
    private ref: ChangeDetectorRef
  ) {
    this.areaForCreate = new Area();
    this.areaForDelete = new Area();
    this.areaDetails = new Area();

    this.areaCount = 0;
    this.countries = this.locationService.countries;
    this.states = this.locationService.states;
    this.cities = this.locationService.cities;
    this.areas = this.areaService.areas;
    this.fetchDone = false;

    this.countryActive = false;
    this.StateActive = false;
    this.CitiesActive = false;
    this.citiesFetched = true;
  }

  ngOnInit() {
    this.areaService.getAreas().subscribe();
    this.areas.subscribe(data => {
      this.areaCount = data.length;
    });

    this.cities.subscribe(data => {
      const cityIds = [];

      this.areaForCreate.cities.forEach(city => {
        data.forEach(v => {
          if (city.toLowerCase() === v.label.toLowerCase()) {
            cityIds.push(v.value);
          }
        });
      });

      this.onCitiesChange(cityIds);
    });

    this.states.subscribe(data => {
      data.forEach(v => {
        if (this.areaForCreate.state.toLowerCase() === v.label.toLowerCase()) {
          this.onStateChange(v.value);
        }
      });
    });
  }

  onCountryChange(countryId) {
    if (countryId) {
      this.selectedCountry = countryId;
      this.locationService.getStates(this.selectedCountry);

      this.countries.value.forEach(v => {
        if (v.value === this.selectedCountry) {
          this.areaForCreate.country = v.label;
        }
      });
    }
  }

  onStateChange(stateId) {
    if (stateId) {
      this.locationService.getCities(stateId);
      this.states.value.forEach(v => {
        if (v.value === stateId) {
          this.areaForCreate.state = v.label;
        }
      });
      this.selectedState = stateId;
      this.citiesFetched = true
      this.ref.detectChanges();
    }
  }

  onCitiesChange(cityIds) {
    if (cityIds && cityIds.length > 0) {
      const cities = [];

      cityIds.forEach(v => {
        return this.cities.value.forEach(p => {
          if (v === p.value) {
            cities.push(p.label);
          }
        });
      });

      this.selectedCities = cityIds;
      this.areaForCreate.cities = cities;
      this.ref.detectChanges();
    }
  }

  showAreaDetail(area: Area) {
    this.areaDetails = area;
    this.displayCard.show();
  }

  openCreateModal() {
    this.citiesFetched = true;
    this.areaForCreate = new Area();
    this.selectedCountry = "";
    this.selectedState = "";
    this.selectedCities = [];
    this.aooForm.reset();
    this.createAreaModal.show();
  }

  createArea() {
    this.areaService.createOrUpdate(Object.assign(new Area(), this.areaForCreate));
    this.createAreaModal.hide();
    this.displayCard.hide();
  }

  updateArea(area: Area) {
    this.citiesFetched = false;
    this.areaForCreate = Object.assign(new Area(), area);

    this.countries.value.forEach(v => {
      if (v.label.toLowerCase() === this.areaForCreate.country.toLowerCase()) {
        this.onCountryChange(v.value);
      }
    });

    this.createAreaModal.show();
  }

  promptAreaDelete(area: Area) {
    this.deleteCard.show();
    this.areaForDelete = Object.assign(new Area(), area);
  }

  removeArea(area: Area) {
    this.displayCard.hide();
    this.deleteCard.hide();
    this.areaService.deleteArea(area);
  }

  searchValueChange(q) {
    this.searchQuery = q;
    this.areaService.searchTerm.next(this.searchQuery);
  }
}



