import { SharedModule } from "app/shared/shared.module";
import { NgModule, NO_ERRORS_SCHEMA } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { AreaOfOperationComponent } from "app/area-of-operation/area-of-operation.component";

export const routes: Routes = [{ path: "", component: AreaOfOperationComponent }];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [AreaOfOperationComponent],
  schemas: [NO_ERRORS_SCHEMA]
})
export class AreaOfOperationModule {}



